<template>
  <div class="addBlog">
    <div class="addMain">
      <div class="row">
        <div class="text">博客标题：</div>
        <div class="ipt"><input type="text" v-model="myObj.title" /></div>
      </div>
      <div class="row">
        <div class="text">作者：</div>
        <div class="ipt"><input type="text" v-model="myObj.author" /></div>
      </div>
      <div class="row">
        <div class="text">分类：</div>
        <div class="ipt">
          <select v-model="myObj.classify">
            <option v-for="item in classList" :key="item.id">
              {{ item.classname }}
            </option>
          </select>
        </div>
      </div>
      <div class="row">
        <div class="text">博客内容：</div>
        <div class="ipt">
          <textarea v-model="myObj.content"></textarea>
        </div>
      </div>
      <div class="row">
        <div class="text"></div>
        <div class="ipt">
          <button @click="onSubmit">发表博客</button>
        </div>
      </div>
    </div>
    <div
      class="showBox"
      v-show="
        myObj.title ||
        this.myObj.content ||
        this.myObj.classify ||
        this.myObj.author
      "
    >
      <p>博客预览</p>
      <BlogItem :myObj="myObj" />
    </div>
  </div>
</template>

<script>
import BlogItem from '@/components/BlogItem'
export default {
  data() {
    return {
      myObj: {
        title: '',
        content: '',
        author: '',
        posttime: new Date().getTime() / 1000,
        classify: ''
      },
      classList: []
    }
  },
  components: {
    BlogItem
  },
  created() {
    this.getClassify()
  },
  methods: {
    // 点击提交
    onSubmit() {
      if (
        this.myObj.title == '' ||
        this.myObj.content == '' ||
        this.myObj.classify == '' ||
        this.myObj.author == ''
      ) {
        alert('请填写完毕再提交')
        return
      }
      this.pushData()
      alert('发表成功')
      this.$router.push('/')
    },
    pushData() {
      this.axios({
        url: 'https://ku.qingnian8.com/dataApi/blog/addBlog.php',
        params: this.myObj
      }).then((res) => {
        console.log(res)
      })
    },
    // 获取分类列表
    getClassify() {
      this.axios({
        url: 'https://ku.qingnian8.com/dataApi/blog/classBlog.php'
      }).then((res) => {
        this.classList = res.data
      })
    }
  }
}
</script>

<style lang="less" scoped>
.addBlog {
  padding: 55px;
  .showBox {
    font-size: 20px;
    min-height: 300px;
    padding-top: 20px;
    border-top: 1px solid #e4e4e4;
  }
  p {
    text-align: center;
  }
  .addMain {
    width: 700px;
    margin: 0 auto 40px;
    .row {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 10px 0;
      .text {
        font-size: 20px;
        font-weight: 400;
        color: #333;
      }
      .ipt {
        width: 80%;
        border: 1px solid #fff;
        input {
          width: 100%;
          height: 40px;
          outline: none;
          padding: 0 10px;
          &:focus {
            box-shadow: 0 0 12px rgba(65, 184, 131, 0.3);
          }
        }
        select {
          width: 40%;
          height: 40px;
        }
        textarea {
          width: 100%;
          height: 200px;
          resize: none;
          outline: none;
          padding: 10px;
          &:focus {
            box-shadow: 0 0 12px rgba(65, 184, 131, 0.3);
          }
        }
        button {
          width: 50%;
          height: 40px;
          font-size: 18px;
          color: #fff;
          background: #41b883;
          border: none;
          border-radius: 5px;
          cursor: pointer;
          transition: 0.4s;
          &:hover {
            background: olivedrab;
          }
        }
      }
    }
  }
}
</style>
